import React, {useState} from 'react';
import logo from '../../assets/images/logo.png';
import reddit from '../../assets/images/icons/reddit.svg';
import medium from '../../assets/images/icons/medium.svg';
import discord from '../../assets/images/icons/discord.svg';
import telegram from '../../assets/images/icons/telegram.svg';
import twitter from '../../assets/images/icons/twitter.svg';
import instagram from '../../assets/images/icons/instagram.svg';
import menu from '../../assets/images/menu.png';
const Header = () => {
    const [openMenu,setMenuOpen] = useState(false)
    return (
        <div className='header'>
            <div className={`mobileMenu ${openMenu ? 'openMenu' : ''}`}>
                <div className="nav">
                    <a href="#space" onClick={()=>{setMenuOpen(false)}}>Apes Of Space VX</a>
                    {/*<a href="#space">Apes Of Space</a>*/}
                    <a href="#lady" onClick={()=>{setMenuOpen(false)}}>Lady Apes</a>
                    <a href="#roadmap" onClick={()=>{setMenuOpen(false)}}>Roadmap</a>
                    {/*<a href="#faq">FAQ</a>*/}
                    <a href="#team" onClick={()=>{setMenuOpen(false)}}>Team</a>
                    <a href="#investor" onClick={()=>{setMenuOpen(false)}}>For Investors</a>
                    <a href="#about" onClick={()=>{setMenuOpen(false)}}>About</a>
                </div>
                <div className="socials">
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={reddit} alt=""/>*/}
                    {/*</a>*/}
                    <a href="https://discord.com/invite/rRrnHCQsvn" target='_blank' rel='noopener noopener noreferrer'>
                        <img src={discord} alt=""/>
                    </a>
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={medium} alt=""/>*/}
                    {/*</a>*/}
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={telegram} alt=""/>*/}
                    {/*</a>*/}
                    <a href="https://twitter.com/apesofspace_nft " target='_blank' rel='noopener noopener noreferrer'>
                        <img src={twitter} alt=""/>
                    </a>
                    <a href="https://www.instagram.com/apesofspace_nft/" target='_blank' rel='noopener noopener noreferrer'>
                        <img src={instagram} alt=""/>
                    </a>
                </div>
                <div className='closeButton' onClick={()=>{setMenuOpen(false)}}>x</div>
            </div>
            <div className="container">
                <div className="logo"><img src={logo} alt="logo"/></div>
                <nav>
                    <a href="#space" >Apes Of Space VX</a>
                    {/*<a href="#space">Apes Of Space</a>*/}
                    <a href="#lady">Lady Apes</a>
                    <a href="#roadmap">Roadmap</a>
                    {/*<a href="#faq">FAQ</a>*/}
                    <a href="#team">Team</a>
                    <a href="#investor">For Investors</a>
                    <a href="#about">About</a>
                </nav>
                <div className="socials">
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={reddit} alt=""/>*/}
                    {/*</a>*/}
                    <a href="https://discord.com/invite/rRrnHCQsvn" target='_blank' rel='noopener noopener noreferrer'>
                        <img src={discord} alt=""/>
                    </a>
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={medium} alt=""/>*/}
                    {/*</a>*/}
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={telegram} alt=""/>*/}
                    {/*</a>*/}
                    <a href="https://twitter.com/apesofspace_nft " target='_blank' rel='noopener noopener noreferrer'>
                        <img src={twitter} alt=""/>
                    </a>
                    <a href="https://www.instagram.com/apesofspace_nft/" target='_blank' rel='noopener noopener noreferrer'>
                        <img src={instagram} alt=""/>
                    </a>
                </div>
                <button className="connectButton">Connect</button>
                <button className='mobileOpenButton' onClick={()=>{setMenuOpen(true)}}>
                    <img src={menu} alt="mobile button"/>
                </button>
            </div>
        </div>
    );
};

export default Header;