import React from 'react';
import SectionTitle from "../section-title";
import lady1 from '../../assets/images/lady1.png';
import lady2 from '../../assets/images/lady2.png';
import lady3 from '../../assets/images/lady3.png';
import lady4 from '../../assets/images/lady4.png';
import lady5 from '../../assets/images/lady5.png';
const LadyApes = () => {
    return (
        <div className='ladyApes' id='lady'>
            <div className="titleContainer">
                <SectionTitle title={'LADY APES'}/>
            </div>
            <div className="items">
                <div className="item small leftHand">
                    <img src={lady4} alt=""/>
                </div>
                <div className="item medium leftHand">
                    <img src={lady2} alt=""/>
                </div>
                <div className="item big">
                    <img src={lady1} alt=""/>
                </div>
                <div className="item medium rightHand">
                    <img src={lady3} alt=""/>
                </div>
                <div className="item small rightHand">
                    <img src={lady5} alt=""/>
                </div>
            </div>
        </div>
    );
};

export default LadyApes;