import React from 'react';
import SectionTitle from "../section-title";
import partner1 from '../../assets/images/partner1.png';
import partner2 from '../../assets/images/partner2.png';
import partner3 from '../../assets/images/partner3.png';
import partner4 from '../../assets/images/partner4.png';
import premiumpartner from '../../assets/images/premiumpartner.png';
import monkey1 from "../../assets/images/monkey1.png";
import monkey2 from "../../assets/images/monkey2.png";
import monkey3 from "../../assets/images/monkey3.png";
const Space = () => {
    return (
        <div className='space' id='space'>
            <div className="titleContainer">
                <SectionTitle title={'APES OF SPACE VX'}/>
            </div>
            <div className="items">
                <div className="item">
                    <img src={monkey1} alt="space monkey"/>
                    <div className="title">Our New Metaverse Home!</div>
                    <p className='des'>
                        Apes of Space has acquired a 6x6 Estate in The Sandbox.
                        Join us in the Metaverse!
                    </p>
                </div>
                <div className="item">
                    <img src={monkey2} alt="space monkey"/>
                    <div className="title">Play2Earn Game</div>
                    <p className='des'>
                        Play unique in-game The Sandbox experiences with Apes of Space VX and earn $SAND tokens by completing quests and challenges
                    </p>
                </div>
                <div className="item">
                    <img src={monkey3} alt="space monkey"/>
                    <div className="title">Premium Access for VX Token Holders</div>
                    <p className='des'>
                        Apes of Space VX Token Holders will benefit from a range of exclusive premium content such as galleries, leaderboards, special events and mini-games
                    </p>
                </div>
            </div>
            <div className="desTitle">The Journey Into The Metaverse</div>
            <p className="desText">
                Apes of Space VX is an NFT ticket that provides access for players to explore unique game experiences. Earn $SAND tokens and other exclusive rewards!
                All The Sandbox players will be able to visit the Ape of Space Hub - a central hub for the community where members can hang-out, socialise and view the full range of locations and quests, however only those with Apes of Space VX will have access to premium in-game experiences and Play2Earn benefits.
            </p>
            <div className="desTitle">How do I get an Apes of Space VX?</div>
            <p className="desText">
                There are three different ways to get hold of a VX: <br/>
                - Airdrop for existing AoS NFT holders. As a reward for early adopters, we will be offering members the chance to win a VX NFT <br/>
                - Daily Social Contests leading up to the launch of the game <br/>
                - Public Sale

            </p>
            {/*<div className="partners">*/}
            {/*    <div className="title">OUR PARTNERS</div>*/}
            {/*    <div className="partnersItems">*/}
            {/*        <img src={partner1} alt=""/>*/}
            {/*        <img src={partner2} alt=""/>*/}
            {/*        <img src={partner3} alt=""/>*/}
            {/*        <img src={partner4} alt=""/>*/}
            {/*    </div>*/}
            {/*</div>*/}
            <div className="premiumPartner">
                <p className="title">OUR PREMIUM PARTNER</p>
                <img src={premiumpartner} alt="premium partner"/>
            </div>
            <div className="video">
                <iframe width="706" height="397" src="https://www.youtube.com/embed/sjWo-SOx29o"
                        title="YouTube video player" frameBorder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        allowFullScreen></iframe>
            </div>
        </div>
    );
};

export default Space;