import React from 'react';
import SectionTitle from "../section-title";
import aboutimage1 from '../../assets/images/aboutimage1.png'
import aboutimage2 from '../../assets/images/aboutimage2.png'
const AboutUs = () => {
    return (
        <div className='aboutUs'>
            <div className="titleContainer">
                <SectionTitle title={'ABOUT US'}/>
            </div>
            <div className="items">
                <div className="item">
                    <img src={aboutimage1} alt=""/>
                    <div className="des">
                        <div className="title">WELCOME TO THE APES OF SPACE WORLD</div>
                        <p className="text">
                            For centuries, chimps dominated the jungle,
                            thriving in the lush green tropical rainforest.
                            Seeds, fruits, honey, and insects were in abundance,
                            and there was little to nothing to worry about. Eat,
                            sleep, and have some fun. This was the routine.
                        </p>
                        <p className="text">
                            However, things began to change when man found
                            the jungle and discovered how to make fire. One
                            tree soon turned to two, three, and now the once
                            green jungle is almost a desert.
                        </p>
                        <p className="text">
                            Spartacus who was a simple chimp is now forced to
                            scavenge for food and hide from humans. He grew
                            tired of watching people cut their jungles, build
                            fires, and steal their bananas.
                        </p>
                        <p className="text">
                            As fate would have it, he saw a falling star
                            (or so he thought) late at night and decided
                            that a new home lies in the stars. Along with
                            10000 of his friends and relatives, Spartacus
                            went looking for this new home. While they journeyed,
                            a random SpaceX rocket fell nearby. They went to repair
                            it. And so, the Apes of Space journey begins.
                        </p>
                    </div>
                </div>
                <div className="item row-reverse">
                    <img src={aboutimage2} alt=""/>
                    <div className="des">
                        <div className="title">TRAITS AND SPECIES</div>
                        <p className="text">
                            Besides the rocket parts, there was a bunch of other junk in the jungle, so the monkeys gathered different pieces of clothes, randomly put them on, and made selfies in some nice locations. The luckiest (and rarest) ones managed to get matching pieces and the others got what they could. We have a total of 120 different variants of equipment and backgrounds in this collection so each monkey is a sight o see!
                        </p>
                    </div>
                </div>
                <div className="item">
                    <img src={aboutimage1} alt=""/>
                    <div className="des">
                        <div className="title">APE IN A BOX</div>
                        <p className="text">
                            We don’t know which monkey gets what clothes, it will be revealed only when a monkey gets into the rocket (when you mint a card). But we know for sure that some of them are truly unique, like Spartacus, Albert, Miss Able, Enos, Gordo, Goliath, Rafiki, Betsy, Pishgam, Sam, Winston and Scatback. Corresponding card numbers of those glorious apes will be announced later.
                        </p>
                        <p className="text">
                            The price of each Apes of Space NFT card is 0.035 Ether, they are distributed on the Ethereum network, and there is a total of 10000 cards.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default AboutUs;